// Entry point for the build script in your package.json
import "@hotwired/turbo-rails"
import "./controllers"
import * as bootstrap from "bootstrap"

import "trix/dist/trix";
import "@rails/actiontext"
import "trix"
import "@rails/actiontext"
import flatpickr from "flatpickr";

document.addEventListener('DOMContentLoaded', function() {
  flatpickr("#deadline-picker", {
    enableTime: true,  // Enable time selection as well
    dateFormat: "Y-m-d H:i",  // Format for the date and time
    minDate: "today",  // Minimum selectable date is today
    defaultHour: 12,   // Default hour to show in the time picker
  });
});